import { FC } from "react";
import Link from "next/link";
import Image from "next/image";

async function CategoryGrid() {
    return (
        <section className="w-full py-6">
            <div className="container mx-auto">
                <div className="flex flex-nowrap md:flex-wrap items-start justify-start md:justify-center gap-4 sm:gap-6 lg:gap-8 overflow-x-auto pb-4 scrollbar-hide px-2">

                    {/* Category 1: Ramadan Essentials */}
                    <Link href="/search/ramadan-essentials" className="group flex flex-col items-center gap-3 min-w-[100px] sm:min-w-[120px] transition-transform hover:scale-105">
                        <div className="relative mt-7 h-20 w-20 sm:h-28 sm:w-28 lg:h-32 lg:w-32 bg-[#0093D0] rounded-[20px] shadow-sm flex items-center justify-center">
                            <Image
                                src="/image/ramadan.png"
                                alt="Ramadan Essentials"
                                fill
                                priority
                                className="object-contain p-2"
                                sizes="(max-width: 768px) 80px, 128px"
                            />
                        </div>
                        <span className="max-w-[110px] text-center text-xs font-bold text-[#8B1D1D] sm:text-sm lg:text-base">Ramadan Essentials</span>
                    </Link>

                    {/* Category 2: Men's Fashion */}

                    <Link href="/search/mens-fashion" className="group flex flex-col items-center gap-3 min-w-[100px] sm:min-w-[120px] transition-transform hover:scale-105">
                        
                        <div className="relative mt-8 h-20 w-20 sm:h-28 sm:w-28 lg:h-32 lg:w-32 bg-[#0093D0] rounded-[20px] shadow-sm flex items-center justify-center">
                            <Image
                                src="/image/men.png"
                                alt="Men's Fashion"
                                width={200}  
                                height={150} 
                                priority
                                // className="object-contain z-10"
                            />
                        </div>

                        <span className="mt-2 max-w-[110px] text-center text-xs font-bold text-[#8B1D1D] sm:text-sm lg:text-base">
                            Men's Fashion
                        </span>
                    </Link>
                    {/* Category 3: Women's Fashion */}
                    <Link href="/search/womens-fashion" className="group flex flex-col items-center gap-3 min-w-[100px] sm:min-w-[120px] transition-transform hover:scale-105">
                        <div className="relative mt-7 h-20 w-20 sm:h-28 sm:w-28 lg:h-32 lg:w-32 bg-[#0093D0] rounded-[20px] shadow-sm flex items-center justify-center">
                            <Image
                                src="/image/women.png"
                                alt="Women's Fashion"
                                fill
                                className="object-contain p-2"
                                sizes="(max-width: 768px) 80px, 128px"
                            />
                        </div>
                        <span className="max-w-[110px] text-center text-xs font-bold text-[#8B1D1D] sm:text-sm lg:text-base">Women's Fashion</span>
                    </Link>

                    {/* Category 4: Gadgets */}
                    <Link href="/search/gadgets" className="group flex flex-col items-center gap-3 min-w-[100px] sm:min-w-[120px] transition-transform hover:scale-105">
                        <div className="relative mt-7 h-20 w-20 sm:h-28 sm:w-28 lg:h-32 lg:w-32 bg-[#0093D0] rounded-[20px] shadow-sm flex items-center justify-center">
                            <Image
                                src="/image/gadget.png"
                                alt="Gadgets"
                                fill
                                className="object-contain p-2 grayscale group-hover:grayscale-0 transition-all"
                                sizes="(max-width: 768px) 80px, 128px"
                            />
                        </div>
                        <span className="max-w-[110px] text-center text-xs font-bold text-[#8B1D1D] sm:text-sm lg:text-base">Gadgets</span>
                    </Link>

                    {/* Category 5: Home Appliances */}
                    <Link href="/search/home-appliance" className="group flex flex-col items-center gap-3 min-w-[100px] sm:min-w-[120px] transition-transform hover:scale-105">
                        <div className="relative mt-7 h-20 w-20 sm:h-28 sm:w-28 lg:h-32 lg:w-32 bg-[#0093D0] rounded-[20px] shadow-sm flex items-center justify-center">
                            <Image
                                src="/image/home_appliance.png"
                                alt="Home Appliances"
                                fill
                                className="object-contain p-2"
                                sizes="(max-width: 768px) 80px, 128px"
                            />
                        </div>
                        <span className="max-w-[110px] text-center text-xs font-bold text-[#8B1D1D] sm:text-sm lg:text-base">Home Appliances</span>
                    </Link>

                    {/* Category 6: Kids Fashion */}
                    <Link href="/search/kids-fashion" className="group flex flex-col items-center gap-3 min-w-[100px] sm:min-w-[120px] transition-transform hover:scale-105">
                        <div className="relative mt-7 h-20 w-20 sm:h-28 sm:w-28 lg:h-32 lg:w-32 bg-[#0093D0] rounded-[20px] shadow-sm flex items-center justify-center">
                            <Image
                                src="/image/kids_fasions.png"
                                alt="Kids Fashion"
                                fill
                                className="object-contain p-2"
                                sizes="(max-width: 768px) 80px, 128px"
                            />
                        </div>
                        <span className="max-w-[110px] text-center text-xs font-bold text-[#8B1D1D] sm:text-sm lg:text-base">Kids Fashion</span>
                    </Link>

                    {/* Category 7: Home Decor */}
                    <Link href="/search/home-decor" className="group flex flex-col items-center gap-3 min-w-[100px] sm:min-w-[120px] transition-transform hover:scale-105">
                        <div className="relative mt-7 h-20 w-20 sm:h-28 sm:w-28 lg:h-32 lg:w-32 bg-[#0093D0] rounded-[20px] shadow-sm flex items-center justify-center">
                            <Image
                                src="/image/home_decors.png"
                                alt="Home Decor"
                                fill
                                className="object-contain p-2"
                                sizes="(max-width: 768px) 80px, 128px"
                            />
                        </div>
                        <span className="max-w-[110px] text-center text-xs font-bold text-[#8B1D1D] sm:text-sm lg:text-base">Home Decor</span>
                    </Link>

                </div>
            </div>
        </section>
    );
}

export default CategoryGrid;





